import React from "react";
import SectionHeader from "../sectionheader/sectionHeader";
import "./registry.css";

export default function Registry() {
    return (
        <section className="section-header" id="registry">
            <SectionHeader name="Registry"></SectionHeader>
            <section className="registry-content">
                <p>
                    Your blessings and presence are all we want.
                    But if you do wish to get us something, do have a look at our Wishlist,
                    so your gift can become a memorable part of our new life.
                </p>
            </section>
        </section>
    )
};