import React from 'react';
import './card.css';

export default function Card(props) {
    let data = props.data ? props.data : {}, flag = props.flag;
    return (
        <section className={flag ? "card left" : "card"}>
            <section className="event-description">
                <h3>{data.name}</h3>
                <span className="day">{data.date}</span>
                <span className="time">{data.time} onwards</span>
                <address>
                    <span className="address">{data.address}</span>
                    <span className="venue">{data.venue}<br /></span>
                    <section><a href={data.map} target="_blank" rel="noopener noreferrer">View in Map</a></section>
                    <p className="details">{data.details}</p>
                </address>
            </section>
        </section>
    )
}