import React from "react";
import SectionHeader from "../sectionheader/sectionHeader";
import "./guestBook.css";

export default class GuestBook extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showMsg: false,
            msg: null
        }
    }

    handleForm = (event) => {
        event.preventDefault();
        let formData = new FormData(event.target),
            obj = {
                name: formData.get("name"),
                email: formData.get("email"),
                msg: formData.get("msg"),
                flag: formData.get("aya")
            };
        fetch("/guest", {
            method: 'POST',
            body: JSON.stringify(obj),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            }
        }).then((result) => {
            result.json().then((res) => {
                let msg = res ? res.msg : "Something went wrong,Please react out us at 910 888 7043.";
                // if (res.status === 200) {
                //     msg = "Entry Added sucessfully!!"
                // } else if (res.status === 400) {
                //     msg = res.body.msg;
                // }
                this.setState({
                    showMsg: true,
                    msg
                });
                setTimeout(() => {
                    this.setState({
                        showMsg: false,
                        msg: null
                    });
                }, 2000);
            })
        });
    }
    render = () => {
        return (
            <section className="guest-container section-header" id="guest">
                <SectionHeader name="Guest Book"></SectionHeader>
                <section className="guest-form">
                    <form id="myform" onSubmit={this.handleForm}>
                        <section className="row">
                            <input type="text" placeholder="Your Name" name="name" required />
                            <input type="Email" placeholder="Your Email" name="email" required />
                        </section>
                        <section className="row">
                            <textarea rows="4" placeholder="Your Message" name="msg" required />
                        </section>
                        <section className="row options">
                            <label className="ques">Are you attending?:</label>
                            <input type="radio" name="aya" value="Y" /><label>YES</label>
                            <input type="radio" name="aya" value="N" /><label>NO</label>
                            <input type="radio" name="aya" value="NS" /><label>NOT SURE</label>
                        </section>
                        <section className="row">
                            <input className="add-to-guest" type="submit" value="ADD TO GUEST BOOK"></input>
                        </section>
                    </form>
                    <section className={this.state.showMsg ? "msg" : "hide"}>
                        {this.state.msg}
                    </section>
                </section>
            </section>)
    }
}