import React from "react";
import "./story.css";
import SectionHeader from "../sectionheader/sectionHeader";
import storyone from "../../assets/story1.jpg";
import storytwo from "../../assets/story2.jpg";

export default function Story() {
    return (
        <section className="section-header" id="story">
            <SectionHeader name="Our Story"></SectionHeader>
            <section className="story-content">
                <h5 className="vertical-divider"></h5>
                <section className="left-section">
                    <article className="met-section">
                        <section className="arrow-cls">
                            <span className="shape">2016</span>
                        </section>
                        <figure>
                            <img src={storyone} alt="story1"></img>
                        </figure>
                        <p>
                            He always says that choosing to miss his flight to talk to me was the best decision he's made in his life..
                        </p>
                    </article>
                </section>
                <section className="right-section">
                    <article className="met-section met-section-right">
                        <section className="arrow-cls right-arrow-cls">
                            <span className="shape">2019</span>
                        </section>
                        <figure>
                            <img src={storytwo} alt="story1"></img>
                        </figure>
                        <p>
                            He proposed on a Scrabble board. It's a game we both love. He spelled,
                            'WILL YOU MARRY ME'? I framed the board; it now hangs in our home!
                        </p>
                    </article>
                </section>
            </section>

        </section>
    )
}