import React, { useContext } from "react";
import { dataContext } from "../../context";
import SectionHeader from "../sectionheader/sectionHeader";
import "./functions.css";

import Mehendi from "../../assets/mehendi.png";
import Haldi from "../../assets/haldi.png";
import Sangeet from "../../assets/sangeet.png";
import Wedding from "../../assets/wedding.png";


import Card from "../card/card";

export default function Functions() {
    let serverData = useContext(dataContext), events = serverData.events ? serverData.events : [];
    return (
        <section className="section-header" id="functions">
            <SectionHeader name="Functions"></SectionHeader>
            {events.map((obj, ind) => {
                let imgSrc = null;
                switch (obj.name) {
                    case "Mehendi": { imgSrc = Mehendi; break; }
                    case "Haldi": { imgSrc = Haldi; break; }
                    case "Sangeet": { imgSrc = Sangeet; break; }
                    case "Wedding": { imgSrc = Wedding; break; }
                    default: { imgSrc = "Haldi" }
                }
                return (
                    <section className={ind % 2 === 0 ? "functions-content row" : "functions-content row block"} key={ind}>
                        <section className={ind % 2 === 0 ? "img-content" : "img-content float-right"}>
                            <figure>
                                <img src={imgSrc} alt="our" width="457" height="614"></img>
                            </figure>
                        </section>
                        <section className={ind % 2 === 0 ? "card-space" : "card-space float-left"}>
                            <Card data={obj} flag={ind % 2 !== 0}></Card>
                        </section>
                    </section>
                )
            })}

        </section>
    )
};