import React, { useContext } from "react";
import SectionHeader from "../sectionheader/sectionHeader";
import { dataContext } from "../../context";
import "./travels.css";

export default function Travels() {
    let temp = useContext(dataContext),
        data = temp.travels ? temp.travels : [];
    return (
        <section className="section-header" id="travels">
            <SectionHeader name="Travels"></SectionHeader>
            <section className="travels-content">
                {
                    data.map((obj, ind) => {
                        return <React.Fragment key={ind}>
                            <h3>{obj.name}</h3>
                            <p>{obj.details}</p>
                        </React.Fragment>
                    })
                }
            </section>
        </section>
    )
};